import axios from 'axios';
import config from './config';
import Cookies from "js-cookie";
import router from '@/router'
// @ == src
import { Message } from 'element-ui'

// vuexloading
// import store from '@/store'

export default function $axios(options) {
  return new Promise((resolve, reject) => {
    const instance = axios.create({
      baseURL: config.baseUrl,
      headers: config.headers,
      timeout: config.timeout,
      withCredentials: config.withCredentials
    })

    // request 
    instance.interceptors.request.use(
      config => {
        let token = Cookies.get('token')
        // 1.  vuex  loading 
        // console.log(store.state.loading)
        // console.log('...')
        // 2. token
        if (token) {
          config.headers['Authorization'] = 'Basic ' + token
        } else {
          // 
          router.push('/login')
        }
        // 3. 
        if (config.method === 'post') {
          // if (config.data.__proto__ === FormData.prototype
          //   || config.url.endsWith('path')
          //   || config.url.endsWith('mark')
          //   || config.url.endsWith('patchs')
          // ) {

          // } else {
            // config.data = qs.stringify(config.data)
          // }
        }

        return config
      },

      error => {
        // 
        console.log('request:', error)
        // 1. 
        if (error.code === 'ECONNABORTED' && error.message.indexOf('timeout') !== -1) {
          console.log('timeout')
          // return service.request(originalRequest);// 
        }
        // 2. 
        const errorInfo = error.response
        console.log(errorInfo)
        if (errorInfo) {
          error = errorInfo.data  // catch,Promise.reject
          const errorStatus = errorInfo.status; // 404 403 500 ...
          router.push({
            path: `/error/${errorStatus}`
          })
        }
        return Promise.reject(error) // (catch)
      }
    )

    // response 
    instance.interceptors.response.use(
      response => {
        if (response.request.responseType === 'blob') {
          return response
        } else if (response.data.success) {
          return response.data
        } else if (!response.data.success) {
          Message({
            showClose: true,
            type: 'error',
            message: response.data.errorDesc?response.data.errorDesc:response.data.result
          })
          return response.data
        } else {
          return Promise.reject(response.data)
        }
      },
      err => {
        if (err && err.response) {
          if (err.response.data.errorCode === 'R-0006' ||err.response.data.errorCode === 'R-0007'||err.response.data.errorCode === 'R-0003'){
            Message({
              showClose: true,
              type: 'error',
              message: err.response.data.errorDesc
            })
            sessionStorage.removeItem("user")
            Cookies.remove("token")
            router.push('/login')
            return false;
          }else {
            switch (err.response.status) {
              case 400:
                err.message = ''
                break
              case 401:
                err.message = ''
                break
              case 403:
                err.message = ''
                break
              case 404:
                err.message = `: ${err.response.config.url}`
                break
              case 408:
                err.message = ''
                break
              case 500:
                err.message = ''
                break
              case 501:
                err.message = ''
                break
              case 502:
                err.message = ''
                break
              case 503:
                err.message = ''
                break
              case 504:
                err.message = ''
                break
              case 505:
                err.message = 'HTTP'
                break
              default:
            }
          }
          
        }
        console.error(err)
        return Promise.reject(err) // 
      }
    )

    // 
    instance(options).then(res => {
      resolve(res)
      return false
    }).catch(error => {
      reject(error)
    })
  })
}


