import axios from '../axios'


// 
export const save = (data) => {
    return axios({
        url: '/dictIndex/save',
        method: 'post',
        data
    })
}
// 
export const batchDelete = (data) => {
    return axios({
        url: '/dictIndex/delete',
        method: 'post',
        data
    })
}
// 
export const findPage = (data) => {
    return axios({
        url: '/dictIndex/findPage',
        method: 'post',
        data
    })
}

export const getDict = (data) => {
    return axios({
        url: '/dictIndex/getDict',
        method: 'post',
        data
    })
}


